* {
    margin: 0;
}

body {
    background-color: #f5f6fa;
}

.app {
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
}

.app__left {
    flex: 0.9;
}

.app__right {
    float: 0.1;
}

.app__header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    background-color: yellow;
    /*flex-direction: column;*/
    justify-content: space-between;
}

.app__stats {
    display: flex;
    justify-content: space-between;
}

@media (max-width: 990px) {
    .app {
        flex-direction: column;
    }
}